.loader {
	 position: fixed;
	 top: 50%;
	 left: 50%;
	 width: 164px;
	 height: 164px;
	 margin: -80px 0px 0px -80px;
	 background-color: transparent;
	 border-radius: 50%;
	 border: 2px solid #e3e4dc;
}
 .loader:before {
	 content: '';
	 width: 164px;
	 height: 164px;
	 display: block;
	 position: absolute;
	 border: 2px solid rgba(36, 34, 34, 0.781);
	 border-radius: 50%;
	 top: -2px;
	 left: -2px;
	 box-sizing: border-box;
	 clip: rect(0px, 35px, 35px, 0px);
	 z-index: 10;
	 -webkit-animation: rotate infinite;
	         animation: rotate infinite;
	 -webkit-animation-duration: 3s;
	         animation-duration: 3s;
	 -webkit-animation-timing-function: linear;
	         animation-timing-function: linear;
}
 .loader:after {
	 content: '';
	 width: 164px;
	 height: 164px;
	 display: block;
	 position: absolute;
	 border: 2px solid rgba(31, 30, 30, 0.644);
	 border-radius: 50%;
	 top: -2px;
	 left: -2px;
	 box-sizing: border-box;
	 clip: rect(0px, 164px, 150px, 0px);
	 z-index: 9;
	 -webkit-animation: rotate2 3s linear infinite;
	         animation: rotate2 3s linear infinite;
}
 .hexagon-container {
	 position: relative;
	 top: 33px;
	 left: 41px;
	 border-radius: 50%;
}
 .hexagon {
	 position: absolute;
	 width: 40px;
	 height: 23px;
	 background-color: #46abca;
}
 .hexagon:before {
	 content: "";
	 position: absolute;
	 top: -11px;
	 left: 0;
	 width: 0;
	 height: 0;
	 border-left: 20px solid transparent;
	 border-right: 20px solid transparent;
	 border-bottom: 11.5px solid #46abca;
}
 .hexagon:after {
	 content: "";
	 position: absolute;
	 top: 23px;
	 left: 0;
	 width: 0;
	 height: 0;
	 border-left: 20px solid transparent;
	 border-right: 20px solid transparent;
	 border-top: 11.5px solid #46abca;
}
 .hexagon.hex_1 {
	 top: 0px;
	 left: 0px;
	 -webkit-animation: Animasearch 3s ease-in-out infinite;
