.nav-container.active .bar {
    transform: rotate(360deg);
    background-color: transparent;
}

.nav-container.active .bar::before {
    transform: translateY(0) rotate(45deg);
}

.nav-container.active .bar::after {
    transform: translateY(0) rotate(-45deg);
}

.nav-container.active .main {
    -webkit-animation: main-animation 0.5s ease;
            animation: main-animation 0.5s ease;
    cursor: pointer;
    transform: perspective(1300px) rotateY(20deg) translateY(10px) translateZ(310px) scale(0.5);
}

@-webkit-keyframes main-animation {
    from {
        transform: translate(0);
    }
    to {
        transform: perspective(1300px) rotateY(20deg) translateY(10px) translateZ(310px) scale(0.5);
    }
}

@keyframes main-animation {
    from {
        transform: translate(0);
    }
    to {
        transform: perspective(1300px) rotateY(20deg) translateY(10px) translateZ(310px) scale(0.5);
    }
}

.links {
    position: absolute;
    width: 30%;
    right: 0;
    top: 0;
    height: 100vh;
    z-index: 2;
    overflow: hidden;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-left: 10px;
}

ul {
    list-style: none;
}

ul li.active a {
    color: orange ;
}

ul li.active a:active{
    color:orange  ;
}
ul li.active a:hover{
    color:orange ;
}

links a:hover{
    color:orange;
}

.links a {
    text-decoration: none;
    color: #eee;
    padding: 0.7rem 0;
    display: inline-block;
    font-size: 1.8rem;
    font-weight: 300;
    text-transform: uppercase;
    letter-spacing: 1px;
    transition: 0.3s;
    opacity: 0;
    transform: translateY(10px);
    -webkit-animation: hide 0.5s forwards ease;
            animation: hide 0.5s forwards ease;
}

.links p{

    transition: 0.3s;
    opacity: 0;
    transform: translateY(10px);
    -webkit-animation: hide 0.5s forwards ease;
            animation: hide 0.5s forwards ease;

}

a.active{
 color:orange !important;
}

.links a:hover {
    color:orange;
}
/* .links a:focus{
    color:orange;
} */


.nav-container.active .links a {
    -webkit-animation: appear 0.5s forwards ease var(--i);
            animation: appear 0.5s forwards ease var(--i);
}

.nav-container.active .links p {
    -webkit-animation: appear 0.5s forwards ease var(--i);
            animation: appear 0.5s forwards ease var(--i);
}

@-webkit-keyframes appear {
    from {
        opacity: 0;
        transform: translateY(10px);
    }
    to {
        opacity: 1;
        transform: translateY(0px);
    }
}

@keyframes appear {
    from {
        opacity: 0;
        transform: translateY(10px);
    }
    to {
        opacity: 1;
        transform: translateY(0px);
    }
}

@-webkit-keyframes hide {
    from {
        opacity: 1;
        transform: translateY(0px);
    }
    to {
        opacity: 0;
        transform: translateY(10px);
    }
}

@keyframes hide {
    from {
        opacity: 1;
        transform: translateY(0px);
    }
    to {
        opacity: 0;
        transform: translateY(10px);
    }
}

.nshadow {
    position: absolute;
    width: 100%;
    height: 100vh;
    top: 0;
    left: 0;
    transform-style: preserve-3d;
    transform-origin: left;
    transition: 0.5s;
    background-color: #fff;
}

.nshadow.one {
    z-index: -1;
    opacity: 0.15;
}

.nshadow.two {
    z-index: -2;
    opacity: 0.1;
}

.nshadow.three {
    z-index: -3;
    opacity: 0.15;
}

.nshadow.four {
    z-index: -4;
    opacity: 0.05;
}

.nav-container.active .nshadow.one {
    -webkit-animation: shadow-one 0.6s ease-out;
            animation: shadow-one 0.6s ease-out;
    transform: perspective(1300px) rotateY(20deg) translateY(10px) translateZ(215px) scale(0.5);
}

@-webkit-keyframes shadow-one {
    0% {
        transform: translate(0);
    }
    5% {
        transform: perspective(1300px) rotateY(20deg) translateY(10px) translateZ(310px) scale(0.5);
    }
    100% {
        transform: perspective(1300px) rotateY(20deg) translateY(10px) translateZ(215px) scale(0.5);
    }
}

@keyframes shadow-one {
    0% {
        transform: translate(0);
    }
    5% {
        transform: perspective(1300px) rotateY(20deg) translateY(10px) translateZ(310px) scale(0.5);
    }
    100% {
        transform: perspective(1300px) rotateY(20deg) translateY(10px) translateZ(215px) scale(0.5);
    }
}

.nav-container.active .nshadow.two {
    -webkit-animation: shadow-two 0.6s ease-out;
            animation: shadow-two 0.6s ease-out;
    transform: perspective(1300px) rotateY(20deg) translateY(10px) translateZ(120px) scale(0.5);
}

@-webkit-keyframes shadow-two {
    0% {
        transform: translate(0);
    }
    20% {
        transform: perspective(1300px) rotateY(20deg) translateY(10px) translateZ(310px) scale(0.5);
    }
    100% {
        transform: perspective(1300px) rotateY(20deg) translateY(10px) translateZ(120px) scale(0.5);
    }
}

@keyframes shadow-two {
    0% {
        transform: translate(0);
    }
    20% {
        transform: perspective(1300px) rotateY(20deg) translateY(10px) translateZ(310px) scale(0.5);
    }
    100% {
        transform: perspective(1300px) rotateY(20deg) translateY(10px) translateZ(120px) scale(0.5);
    }
}

.nav-container.active .main:hover+.nshadow.one {
    transform: perspective(1300px) rotateY(20deg) translateY(10px) translateZ(230px) scale(0.5);
}

.nav-container.active .main:hover {
    transform: perspective(1300px) rotateY(20deg) translateY(10px) translateZ(340px) scale(0.5);
}



