/* Buttons Card Flipper 
   * Source: https://cssdeck.com/labs/rxcleo5w
   */

.flip-cards {
  width: 100%;
  min-height: 50vh;
  max-height:80vh;
  display: grid;
  grid-template-columns: repeat( auto-fit, minmax(200px, .4fr));
  justify-content: center;
  grid-gap: 30px;
  gap: 30px;
  padding: 30px;
  align-items: center;
  overflow-y: auto;
  border-radius: 15px;
  box-shadow: 0px 0px 15px 1px rgb(218 218 218 / 23%); 
}


.flip-cards div {
  height: 180px;
  position: relative;
  padding: 0;
}

.flip-cards div:hover {
  cursor: pointer;
}

.flip-cards div:hover .card-front {
  transform: rotateY(180deg);
  transform: perspective(1000) rotateY(180deg);
}

.flip-cards div:hover .card-back {
  z-index: 950;
  transform: rotateY(0deg);
  transform: perspective(1000) rotateY(0deg);
}

.flip-cards .card-front, .flip-cards .card-back {
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  transition: transform 0.6s;
  display: block;
  height: 100%;
  position: absolute;
  width: 100%;
}

.flip-cards .card-front {
  transform: perspective(1000) rotateY(0);
  z-index: 900;
}

.flip-cards .card-back {
  transform: rotateY(-180deg);
  z-index: 800;
}

/* Button Card Colors */

.flip-cards .panel {
  border: none;
}

.flip-cards .panel i, .flip-cards .panel p, .flip-cards .panel a, .flip-cards .panel small {
  color: white;
}

